import React from 'react'
import { Container } from "./SliderElements"
import { Carousel } from "react-bootstrap"
import slider1 from "../../img/slider1.jpg"
import slider2 from "../../img/slider2.jpg"
import slider3 from "../../img/slider3.jpg"
import slider4 from "../../img/slider4.jpg"
import slider5 from "../../img/slider5.jpg"
import slider8 from "../../img/slider8.jpeg"



export const Slider = () => {
    return (
        <Container>

            <Carousel className="slider">

                <Carousel.Item interval={2000} style={{ maxWidth: "100vw", maxHeight: "100vh", minHeight: "40vh"}} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider1}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh",  minHeight: "40vh", objectFit: "cover" }}
                        />
                    </div>
                    {/* <Carousel.Caption style={{ top: "50%", transform: "translateY(-50%)", bottom: "initial" }}>
                        <div className="info-txt-slider">
                            <h3>Roupas Formais</h3>
                            <button>Conhecer coleção</button>
                        </div>
                    </Carousel.Caption> */}
                </Carousel.Item>



                <Carousel.Item interval={2000} style={{ maxWidth: "100vw", maxHeight: "100vh", minHeight: "40vh"}} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider2}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh",  minHeight: "40vh", objectFit: "cover" }}
                        />
                    </div>
                </Carousel.Item>






                <Carousel.Item interval={2000} style={{ maxWidth: "100vw", maxHeight: "100vh", minHeight: "40vh"}} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider3}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh",  minHeight: "40vh", objectFit: "cover" }}
                        />
                    </div>
                </Carousel.Item>




                <Carousel.Item interval={2000} style={{ maxWidth: "100vw", maxHeight: "100vh", minHeight: "40vh"}} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider4}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh",  minHeight: "40vh", objectFit: "cover" }}
                        />
                    </div>
                </Carousel.Item>



                
                <Carousel.Item interval={2000} style={{ maxWidth: "100vw", maxHeight: "100vh", minHeight: "40vh"}} className="itemSlider">
                    <div className="item">
                        <img
                            className="d-block w-100"
                            src={slider5}
                            alt="First slide"
                            style={{ maxWidth: "100vw", maxHeight: "100vh",  minHeight: "40vh", objectFit: "cover" }}
                        />
                    </div>
                </Carousel.Item>

            </Carousel>
        </Container>
    )
}
