
import React, { useState } from 'react'
import {
    Container,
    Menu,
    LogoIMG,
    IconMenuMobile,
    MenuDesktop,
    ContainerSub,
    SubMenu,
    SideBar,
    IconFecharMenuMobile,
} from "./NavElements"
import { Link } from 'react-router-dom'
import { FaHeart, FaUserAlt, FaShoppingCart, FaSearch } from "react-icons/fa"

export const Nav = () => {

    const [isOpen, setIsOpen] = useState(false);

    return (
        <>
            <Container>
                <Menu>

                    <LogoIMG />
                    <IconMenuMobile onClick={() => setIsOpen(!isOpen)} />

                    <MenuDesktop>
                        <nav>
                            <ul>
                                <li className="li-busca">
                                    <Link className="link-menu-desktop"><input type="search" name="busca" id="busca" /></Link>
                                    <FaSearch className="icone-search" />
                                </li>

                                <li className="li">
                                    <Link className="link-menu-desktop"><FaHeart className="icone-menu" /><p>Favoritos</p></Link>
                                </li>

                                <li className="li">
                                    <Link className="link-menu-desktop"><FaUserAlt className="icone-menu" /><p>Entrar</p></Link>
                                </li>

                                <li className="li">
                                    <Link className="link-menu-desktop"><FaShoppingCart className="icone-menu" /></Link>
                                </li>
                            </ul>
                        </nav>
                    </MenuDesktop>
                </Menu>

                <ContainerSub style={{ background: "#ff266a", height: "50px" }}>
                    <SubMenu>
                        <nav>
                            <ul>
                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Todos</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Vestidos</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Promoções</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Macaquinhos</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Blusas</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Shorts</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Calças</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Conjuntos</Link>
                                </li>

                                <li className="li">
                                    <Link className="link-submenu-desktop" style={{ textDecoration: "none" }}>Plus Size</Link>
                                </li>

                            </ul>
                        </nav>

                    </SubMenu>
                </ContainerSub>

            </Container>

            <SideBar isOpen={isOpen}>
                <IconFecharMenuMobile onClick={() => setIsOpen(!isOpen)} />

                <nav>
                    <ul>
                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Todos</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Vestidos</Link>
                        </li>
                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Promoções</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Macaquinhos</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Blusas</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Shorts</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Calças</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Conjuntos</Link>
                        </li>

                        <li className="li">
                            <Link className="link-submenu-mobile" style={{ textDecoration: "none" }}>Plus Size</Link>
                        </li>

                        <li className="li li-bottom">
                            <Link className="link-button-bottom-mobile" style={{ textDecoration: "none" }}><FaUserAlt className="icone-menu" /><p>Entrar</p></Link>
                            <Link className="link-button-bottom-mobile link-right" style={{ textDecoration: "none" }}><FaHeart className="icone-menu" /><p>Favoritos</p></Link>
                        </li>


                    </ul>
                </nav>

            </SideBar>
        </>
    )
}


