import styled from "styled-components"


export const Container = styled.div`
    width: 100%;
    text-align: center;
    background: #000;
    padding: 30px 0;
    text-align: center;

    h1{
        font-size: clamp(1.3rem, 2.5vw, 2.5rem);
        color: ${props=>props.theme.ternary_color};
        margin-top: 30px;
        text-transform: uppercase;
    }

    hr{
        width: 80px;
        margin: 20px auto;
        margin-bottom: 50px;
        border: 2px solid ${props=>props.theme.ternary_color};
    }

    .owl-theme{
        width: 100%;
        position: relative;

        .owl-nav .owl-prev,
        .owl-nav .owl-next{
            width: 40px;
            height: 120px;
            outline: none;
            background: #000;
            font-size: 5rem;
            position: absolute;
            top: 35%;
            color: ${props=>props.theme.ternary_color};

            display: flex;
            justify-content: center;
            align-items: center;
        }

        .owl-nav .owl-prev{
            left: 0;
            &:hover{
                background: ${props=>props.theme.ternary_color};
                color: #000;
            }
        }
        .owl-nav .owl-next{
            right: 0;
            &:hover{
                background: ${props=>props.theme.ternary_color};
                color: #000;
            }
        }

        .item{
            cursor: pointer;
            background: ${props=>props.theme.ternary_color};
        

            .box-img{
                height: 80%;
                overflow-x: hidden;
                img{
                    height: 100%;
                    object-fit: cover;   
                    position: relative;
                }

                .icone-favorito{
                    cursor: pointer;
                    width: clamp(30px, 2.5vw, 50px);
                    height: clamp(30px, 2.5vw, 50px);
                    padding: 3px;
                    background: rgba(255, 255, 255, .8);
                    position: absolute;
                    top: 10px;      
                    right: 10px;
                    border-radius:50%;

                    &:hover{
                        color: ${props=>props.theme.ternary_color};
                    }
                }
            }
        }

        .txt-box{
            background: #fff;
            padding-top: 10px;
            padding-bottom: 1px;
        }
    
    }





`