import React from 'react'
import { Container } from "./FooterElements"
import { Row, Col } from "react-bootstrap"
import { Layout } from "../Layout/Index"
import { Link } from "react-router-dom"
import { FaGreaterThan } from "react-icons/fa"
import { SiFacebook } from "react-icons/si"
import facebook_icone from "../../img/facebook.png"
import instagram_icone from "../../img/instagram.png"

export const Footer = () => {
    return (
        <Container>
            <Layout>
                <Row>
                    <Col xm={12} md={4} className="coluna">
                        <h2>Sobre</h2>
                        <p>Dona Flor é a maior rede de moda feminina e lingerie do Brasil. Com quase 70 anos de experiência, construiu uma forte relação de cumplicidade e intimidade com a mulher, conhecendo e acompanhando suas necessidades e anseios.</p>
                        <Link>
                            <img src={facebook_icone} alt="facebook" className="rede-social"/>
                        </Link>

                        <Link>
                            <img src={instagram_icone} alt="instagram" className="rede-social" style={{marginLeft: '10px'}}/>
                        </Link>

                    </Col>

                    <Col xm={12} md={4} className="coluna coluna-links">
                        <nav>
                            <h2>Links</h2>
                            <ul>
                                <li><Link to="/" className="Link" ><FaGreaterThan className="icone" />Sobre</Link></li>
                                <li><Link to="/" className="Link"><FaGreaterThan className="icone" />Lançamentos</Link></li>
                                <li><Link to="/" className="Link"><FaGreaterThan className="icone" />Carreira</Link></li>
                                <li><Link to="/" className="Link"><FaGreaterThan className="icone" />Serviços</Link></li>
                                <li><Link to="/" className="Link"><FaGreaterThan className="icone" />Projetos</Link></li>

                            </ul>
                        </nav>
                    </Col>

                    <Col xm={12} md={4} className="coluna">
                        <h2>Contato</h2>
                        <p>Travessa São Luis, 8520</p>
                        <p>Telefone: (99) 98144 - 4269</p>
                        <p>Email: flanelon18@gmail.com</p>
                    </Col>


                    <Col xm={12} className="coluna">
                        <hr />
                        <h3>© Copyright 2021 by Great Ideas. All Rights Reserved.</h3>
                    </Col>
                </Row>
            </Layout>
        </Container>
    )
}
