import styled from "styled-components"

export const Container = styled.div`
    width: 100%;
    height: fit-content;
    background: ${props=> props.theme.ternary_color};;
    color: #fff;
    padding-top: 80px;

    .coluna{
        margin-bottom: 50px;
        h2{
        font-size: clamp(1.5rem, 2.5vw, 1.7rem);
        margin-bottom: 20px;
        }
        p{
            color: #fff;
        }
        .rede-social{
            width: 40px;
        }
        nav{
            ul{
                list-style: none;
                li{
                    height: 32px;
                    .Link{
                        text-decoration:none;
                        color: #fff;
                        &:hover{
                            padding-left: 8px;
                            color: #11117e;
                            transition: all .4s;
                        }

                        &:hover > .icone{
                            color: #11117e;
                        }

                        .icone{
                            margin-right: 5px;
                        }
                    }
                }
            }
        }
        hr{
            border-top: 1px solid #fff;
        }
        
        h3{
            text-align: center;
            font-size: clamp(1.2rem, 2.5vw, 1.2rem);
        }
    }
    .coluna-links{

        @media (min-width: 956px){
            padding-left: 80px;
        }
    }

`;